<template>
  <div class="powerManage">
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>用户设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 权限设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="75"
      >
        <Row>
          <Col
            span="6"
          >
          <FormItem
            class="labelWidth"
            label="权限组名称"
          >
            <Input
              v-model="searchData.adminRoleName"
              clearable
              placeholder="请填写权限组名称"
            ></Input>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2}"
          >
          <Button
            type="primary"
            @click="fetchData"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="delData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(5)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/permission/exportPermissionListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="permissionList"
        @on-current-change="selectRow"
      ></Table>
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span>请设置权限组信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="24">
                <FormItem
                  label="权限名称"
                  prop="adminRoleName"
                >
                  <Input
                    v-model.trim="form.adminRoleName"
                    :disabled="isDisabled"
                    placeholder="请填写权限名称"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem
                label="选择权限"
                prop="selPower"
              >
                <Tree ref="roleTree" :data="roleTree" show-checkbox :render="renderContent" class="roleTree"></Tree>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem label="备注">
                <Input
                  v-model.trim="form.adminRoleRemark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name2"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="resetType('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
    </Content>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'PowerManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      total: 0,
      currentRow: {},
      isSelected: false,
      isDisabled: false,
      // 权限点选结束
      form: {
        adminRoleName: '',
        adminRolePermission: '',
        adminRoleRemark: ''
      },
      ruleValidateType: {
        adminRoleName: [
          { required: true, message: '请填写权限名称', trigger: 'change' }
        ]
      },
      // 筛选内容
      searchData: {
        adminRoleName: '',
        ascending: false,
        orderBy: 'adminRoleId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      selTypeMod: false,
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '权限组名称',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.adminRoleName);
          }
        }
      ],
      permissionList: [
      ],
      // 权限树
      roleTree: [],
      isCreate: true
    }
  },
  mounted() {
    this.fetchData();
    // 获取menu的树结构
    this.getRoleTree();
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    // 判断节点是否展开
    isExpandNode(id, children, idStr, ids) {
      if (id === 0 && children.length > 0 && (ids && ids.length > 0)) {
        for (const item of children) {
          if (ids.indexOf(item[idStr].toString()) !== -1) return true
        }
      }
      return false;
    },
    handleTree: function(tree, obj, flag, ids) {
      const result = [];
      tree.forEach(item => {
        const id = item[obj.id]
        if (flag && id === 1) return
        const title = item[obj.title];// 展示的名字
        let children = item[obj.children];// 子节点
        const checked = ids ? ids.indexOf(id.toString()) !== -1 : false
        const expand = this.isExpandNode(item[obj.pId], children, obj.id, ids);// 是否展开
        const disabled = this.isDisabled
        // 如果有子节点，递归
        if (children.length > 0) {
          children = this.handleTree(children, obj, false, ids);
        }
        result.push({ expand, title, children, id, checked, disabled });
      });
      return result;
    },
    // 自定义树
    renderContent(h, { root, node, data }) {
      const title = data.title
      if (data.children.length > 0) {
        return h('span', {
          style: {
            fontWeight: 'bolder',
            color: '#2d8cf0'
          }
        }, [h('span', title)])
      }
      return h('span', title)
    },
    // 获取树数据
    getRoleTree(ids, callBack) {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/permission/findMenuInfo', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const obj = {
            id: 'adminMenuId',
            pId: 'adminMenuPid',
            title: 'adminMenuPermissionName', // 接口返回的名称
            children: 'menuModelList'// 接口返回的子节点
          };
          const data = res.data
          const resultTree = that.handleTree(data, obj, true, ids)
          if (resultTree.length > 0) {
            if (resultTree[0].id === 1) {
              that.roleTree = resultTree;
            } else {
              that.roleTree = [{
                title: data[0][obj.title],
                expand: true,
                children: resultTree
              }]
            }
          }
          if (ids) callBack()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        that.$Spin.hide();
      }, that)
    },
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.fetchData()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.fetchData()
    },
    // 获取权限组的数组
    fetchData() {
      const that = this
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/permission/findPermissionList', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const listData = res.data
          that.permissionList = listData.permissionList;
          that.total = listData.totalCount
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    // 删除数据
    delData() {
      const that = this
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/permission/deletePermission/' + that.currentRow.adminRoleId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.fetchData()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    },
    clearFormData() {
      this.form = {
        adminRoleName: '',
        adminRolePermission: '',
        adminRoleRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearFormData()
      this.isDisabled = true;
      this.isCreate = false
      // 接口赋值
      this.form.adminRolePermission = this.getRoleTree(data.adminRolePermission.split(','), () => {
        this.form.adminRoleId = data.adminRoleId
        this.form.adminRoleName = data.adminRoleName
        this.form.adminRoleRemark = data.adminRoleRemark
        this.form.fileids = data.fileids
        this.selTypeMod = true;
      })
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow;
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      const that = this
      const row = Object.assign({}, that.currentRow)
      that.isDisabled = false;
      that.isCreate = false
      that.clearFormData()
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      // 接口赋值
      that.form.adminRolePermission = that.getRoleTree(row.adminRolePermission.split(','), () => {
        that.form.adminRoleId = row.adminRoleId
        that.form.adminRoleName = row.adminRoleName
        that.form.adminRoleRemark = row.adminRoleRemark
        that.form.fileids = row.fileids
        that.$nextTick(() => {
          that.$refs.uploadFile.getFileItems()
        })
        that.selTypeMod = true;
      })
    },
    createData() {
      this.clearFormData();
      // 获取menu的树结构
      this.getRoleTree();
      this.isCreate = true
      this.isDisabled = false;
      this.selTypeMod = true;
    },
    // 筛选树权限的数据
    filterTreeData() {
      const tree = this.$refs.roleTree.getCheckedNodes()
      const result = []
      tree.forEach(item => {
        if (item.children.length === 0) {
          result.push(item.id)
        }
      })
      return result.join(',')
    },
    // 权限点选结束
    submitType(name) {
      const that = this;
      const form = this.form
      form.adminRolePermission = that.filterTreeData()
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 添加
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/permission/addPermission', form, (res) => {
            // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.currentRow = {}
                that.fetchData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPutUtil('/permission/updatePermissionInfo', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.currentRow = {}
                that.fetchData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    }
  }
}
</script>

<style>
.roleTree ul li{
  margin:0px;
}
.roleTree{
  max-height: 300px;
  overflow: auto;
}
</style>




